<template>
  <div class="help-cpnt white">
    <iHeader @hide="$emit('hide')" text=""/>
    <v-img height="34.4vw" :src="helpBanner">
      <div class="fill-height px-4 d-flex flex-column align-end justify-center">

        <span class="mb-0 subtitle-1"></span>
        <p class="mb-0 caption">9:00-18:00()</p>
      </div>
    </v-img>
    <div class="subtitle-1 px-4 py-2"></div>
    <v-expansion-panels>
      <v-expansion-panel
        v-for="(item,i) in list"
        :key="i"
      >
        <v-expansion-panel-header>{{item.title}}</v-expansion-panel-header>
        <v-expansion-panel-content>
          {{item.content}}
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-footer fixed bottom class="pa-0 white">
      <div class="d-flex flex-fill justify-center grey--text caption mb-4"></div>
      <v-btn @click="callService" dark depressed block tile color="primary">
        <v-icon>mdi-phone-in-talk</v-icon>{{serviceLine}}
      </v-btn>
    </v-footer>
  </div>
</template>

<script>
import iHeader from '../public/header'
export default {
  name: '',
  components: {iHeader},
  data: ()=> ({
    list: [
      {
        title: '',
        content: ''
      },
      {
        title: '',
        content: '13'
      },
      {
        title: '',
        content: ''
      },
      {
        title: 'APP',
        content: 'APP'
      },
      {
        title: '',
        content: ''
      },
      {
        title: '',
        content: ''
      },
      {
        title: '',
        content: '()'
      },
    ],
    helpBanner: require('../../assets/img/helpBanner.png'),
    serviceLine: '0591-86393690'

  }),
  mounted(){
    document.addEventListener('backbutton',this.runBack,false)
  },
  beforeDestroy(){
    document.removeEventListener('backbutton',this.runBack,false)
  },
  methods: {
    runBack(){
      this.$emit('hide')
    },
    callService(){
      location.href='tel:' + this.serviceLine
    }
  }
}
</script>

<style  lang="scss" scoped>
.help-cpnt{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  padding-top: 45px;
  overflow-y: auto;
  z-index: 100;
}
.pl44p{
  padding-left: 40%;
}
</style>
